import React, { useState } from 'react';
import { Bell, Home, TrendingUp, Heart, Settings, HelpCircle, User } from 'lucide-react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

const Dashboard = () => {

    const [activeTab, setActiveTab] = useState('home');

    const chartData = [
    { session: 1, performance: 75, mood: 4.0, feedback: 3.8 },
    { session: 2, performance: 60, mood: 3.2, feedback: 3.5 },
    { session: 3, performance: 80, mood: 4.2, feedback: 4.0 },
    { session: 4, performance: 45, mood: 2.5, feedback: 2.8 },
    { session: 5, performance: 55, mood: 3.0, feedback: 3.2 },
    { session: 6, performance: 85, mood: 4.5, feedback: 4.3 },
    { session: 7, performance: 95, mood: 5.0, feedback: 4.8 },
    { session: 8, performance: 90, mood: 4.7, feedback: 4.5 },
    { session: 9, performance: 70, mood: 3.8, feedback: 3.9 },
    { session: 10, performance: 75, mood: 4.0, feedback: 4.0 },
  ];


  return (
    <div className="flex h-screen bg-gray-800">
      {/* Sidebar */}
      <div className="w-64 bg-linear-to-b from-gray-700 to-lime-200 flex flex-col">
        {/* Dashboard Header */}
        <div className="bg-gray-900 text-white p-6 text-center">
          <h1 className="text-2xl font-black tracking-wide">DASHBOARD</h1>
        </div>

        {/* Navigation Menu */}
        <nav className="flex-1 py-4">
          <button
            onClick={() => setActiveTab('home')}
            className={`w-full px-6 py-4 text-left font-black text-xl flex items-center gap-3 transition-colors ${
              activeTab === 'home' ? 'bg-lime-600 text-white' : 'bg-lime-200 text-gray-900 hover:bg-lime-300'
            }`}
          >
            <Home size={24} />
            HOME
          </button>
          <button
            onClick={() => setActiveTab('analysis')}
            className={`w-full px-6 py-4 text-left font-black text-xl flex items-center gap-3 transition-colors ${
              activeTab === 'analysis' ? 'bg-lime-600 text-white' : 'bg-lime-200 text-gray-900 hover:bg-lime-300'
            }`}
          >
            <TrendingUp size={24} />
            ANALYSIS
          </button>
          <button
            onClick={() => setActiveTab('pet')}
            className={`w-full px-6 py-4 text-left font-black text-xl flex items-center gap-3 transition-colors ${
              activeTab === 'pet' ? 'bg-lime-600 text-white' : 'bg-lime-200 text-gray-900 hover:bg-lime-300'
            }`}
          >
            <Heart size={24} />
            PET SUPPORT
          </button>
          <button
            onClick={() => setActiveTab('settings')}
            className={`w-full px-6 py-4 text-left font-black text-xl flex items-center gap-3 transition-colors ${
              activeTab === 'settings' ? 'bg-lime-600 text-white' : 'bg-lime-200 text-gray-900 hover:bg-lime-300'
            }`}
          >
            <Settings size={24} />
            SETTINGS
          </button>
        </nav>

        {/* Bottom Menu */}
        <div className="border-t-2 border-lime-400">
          <button className="w-full px-6 py-4 text-left font-black text-lg flex items-center gap-3 bg-lime-200 text-gray-900 hover:bg-lime-300">
            <HelpCircle size={20} />
            HELP
          </button>
          <button className="w-full px-6 py-4 text-left font-black text-lg flex items-center gap-3 bg-lime-200 text-gray-900 hover:bg-lime-300">
            <User size={20} />
            PROFILE
          </button>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 bg-gray-100 overflow-auto">
        {/* Header */}
        <div className="bg-white px-8 py-6 flex justify-between items-center shadow-sm">
          <div>
            <h1 className="text-4xl font-black text-gray-900">WELCOME BACK!</h1>
            <h2 className="text-4xl font-black text-gray-900">USER</h2>
          </div>
          <div className="flex items-center gap-4">
            <button className="p-3 bg-lime-200 rounded-full hover:bg-lime-300 transition-colors">
              <Bell size={24} className="text-gray-900" />
            </button>
            <button className="px-8 py-3 bg-lime-500 text-white font-black text-xl rounded-full hover:bg-lime-600 transition-colors">
              LOG OUT
            </button>
          </div>
        </div>

        {/* Content Area */}
        <div className="p-8">
          <div className="grid grid-cols-2 gap-8">
            {/* Left Column - Action Buttons */}
            <div className="space-y-4">
              <button className="w-full px-8 py-6 bg-lime-400 text-gray-900 font-black text-2xl rounded-3xl hover:bg-lime-500 transition-colors shadow-lg">
                MONITOR YOUR MOOD
              </button>
              <button className="w-full px-8 py-6 bg-lime-300 text-gray-900 font-black text-2xl rounded-3xl hover:bg-lime-400 transition-colors shadow-lg">
                QUICK QUIZ
              </button>
              <button className="w-full px-8 py-6 bg-lime-200 text-gray-900 font-black text-2xl rounded-3xl hover:bg-lime-300 transition-colors shadow-lg">
                TODAY&apos;S TIP
              </button>
            </div>

            {/* Right Column - Chart */}
            <div className="bg-white p-6 rounded-3xl shadow-lg">
              <h3 className="text-sm font-semibold text-gray-700 mb-4">
                User Performance Analysis: Mood vs. Score Over Sessions
              </h3>
              <ResponsiveContainer width="100%" height={300}>
                <LineChart data={chartData}>
                  <CartesianGrid strokeDasharray="3 3" stroke="#e5e7eb" />
                  <XAxis 
                    dataKey="session" 
                    label={{ value: 'Session Number', position: 'insideBottom', offset: -5 }}
                  />
                  <YAxis 
                    yAxisId="left"
                    label={{ value: 'Performance Score', angle: -90, position: 'insideLeft' }}
                  />
                  <YAxis 
                    yAxisId="right" 
                    orientation="right"
                    domain={[0, 5]}
                    label={{ value: 'Mood', angle: 90, position: 'insideRight' }}
                  />
                  <Tooltip />
                  <Legend />
                  <Line 
                    yAxisId="left"
                    type="monotone" 
                    dataKey="performance" 
                    stroke="#3b82f6" 
                    name="Performance Score"
                    strokeWidth={2}
                    dot={{ fill: '#3b82f6', r: 4 }}
                  />
                  <Line 
                    yAxisId="right"
                    type="monotone" 
                    dataKey="mood" 
                    stroke="#ef4444" 
                    name="User Mood"
                    strokeWidth={2}
                    dot={{ fill: '#ef4444', r: 4 }}
                  />
                  <Line 
                    yAxisId="right"
                    type="monotone" 
                    dataKey="feedback" 
                    stroke="#f59e0b" 
                    name="Feedback Score"
                    strokeWidth={2}
                    dot={{ fill: '#f59e0b', r: 4 }}
                  />
                </LineChart>
              </ResponsiveContainer>
            </div>
          </div>

          {/* Bottom Section - Monthly Stats */}
          <div className="mt-8 grid grid-cols-4 gap-6">
            <div className="col-span-1">
              <h2 className="text-4xl font-black text-lime-700 leading-tight">
                THIS<br />MONTH
              </h2>
            </div>
            <div className="bg-lime-400 p-8 rounded-3xl text-center shadow-lg">
              <h3 className="text-xl font-black text-gray-900 mb-2">HAPPY<br />DAYS</h3>
              <p className="text-6xl font-black text-gray-900">17</p>
            </div>
            <div className="bg-red-400 p-8 rounded-3xl text-center shadow-lg">
              <h3 className="text-xl font-black text-gray-900 mb-2">SAD DAYS</h3>
              <p className="text-6xl font-black text-gray-900">6</p>
            </div>
            <div className="bg-yellow-300 p-8 rounded-3xl text-center shadow-lg">
              <h3 className="text-xl font-black text-gray-900 mb-2">STRESSED<br />DAYS</h3>
              <p className="text-6xl font-black text-gray-900">4</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Dashboard